.home-container{

    background: url("../assets/home/background-home-mobile.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: -1px;
    min-height: 100vh;
    padding-bottom: 90px;

    @media screen and (min-width:768px) {
        background: url("../assets/home/background-home-tablet.jpg");
        background-size: cover;
        background-repeat: no-repeat;
    }

    @media screen and (min-width: 1064px) {
        background: url("../assets/home/background-home-desktop.jpg");
        background-size: cover;
        background-repeat: no-repeat;
    }
    .home-content{
        text-align: center;
        @media screen and (min-width: 1064px) {
            display: flex;
            margin-top: 251px;
            text-align: left;
            max-width: 1110px;
            justify-content: space-between;
        }
    }

    h5{
        margin:0 ;
        color: $color1;
    }

    .hero-content{
        margin: 48px auto 81px;

        @media screen and (min-width:768px) {
            margin:106px auto 156px ;
            width: 450px;
         }

         @media screen and (min-width: 1064px) {
            margin:0;
            display: flex;
            flex-direction: column;

            width: 444px;
        }
    }

    .circle-content{
        background-color: $white;
        border-radius: 100%;
        width: 150px;
        height: 150px;
        display: flex;
        justify-content: center;
        align-items: center;
        color: black;
        margin: 0 auto;
        position: relative;
        animation: breathPlanet 4s ease infinite alternate none;
        cursor: pointer;
        h4{
            color: #0B0D17;
        }
        @media screen and (min-width:768px) {
            width: 242px;
            height: 242px;
            h4{
                @include fontStyle(2rem,400,150px,0 );

            }
            
        }

        @media screen and (min-width:1064px) {
           align-self: flex-end;
           margin: 0;
        }

        &::before{
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            width:150px ;
            height: 150px;
            background-color: $white;
            border-radius: 1000px;
            opacity: 10%;
            transition: all 0.7s ease;

           
        }

        &:hover::before{
            width:250px ;
            height: 250px;

            //Tablette
            @media screen and (min-width:768px) {
                width: 342px;
                height: 342px;
            }
        }
    }

    h1{
        margin: 16px 0 16px;
        @media screen and (min-width:768px) {
           margin: 24px auto;
           @include fontStyle(9.375rem,400,150px,0 );
    
         }

         
     @media screen and (min-width:1064px) {
        @include fontStyle(9.375rem,400,normal,0 );
        margin: 24px auto 20px;
    }
    }
}

@keyframes breathPlanet {
    0%{
        transform: scale(1);
    }
    50%{
        transform: scale(1.1);
    }

    100%{
        transform: scale(1);
    }  
}