.techno-container{
    background-image: url("../assets/technology/background-technology-mobile.jpg");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;
    @media screen and (min-width:768px) {
        background: url("../assets/technology/background-technology-tablet.jpg");
        background-size: cover;
        background-repeat: no-repeat;
    }

    @media screen and (min-width: 1064px) {
        display: flex;
        flex-direction: column;

        background: url("../assets/technology/background-technology-desktop.jpg");
        background-size: cover;
        background-repeat: no-repeat;
     
}
    .techno-content{
        text-align: center;
        
        @media screen and (min-width: 1064px) {
           flex: 1;
        }

        > div {
            @media screen and (min-width:1064px) {
                display: grid;
                grid-template-areas: "a b c";
                grid-template-columns:  auto auto minmax(0,1fr) ;
                align-items: center;
                justify-content: space-between;
                width: 1110px;
                margin: 137px auto;
                gap: 80px;
               
            }
           
        }

        h5{
            @media screen and (min-width: 768px) {
               text-align: left;
               margin-top: 45px;
               width: 1110px;
              }
        }
        img{
            display: inline;
            margin: 32px 0 34px;
            width: 100%;

            @media screen and (min-width: 1064px) {
                width: 515px;
                position: absolute;
                right: 0;
                justify-self: end;

               }
        }

        .radio-container{
            width: 152px;
            display: flex;
            justify-content: space-between;
            margin: 0 auto 32px;
            @media screen and (min-width:1064px) {
                grid-area: a;
                flex-direction: column;
                height: 304px;
                width: auto;
                margin-bottom: 0;
            }
            input[type=radio]{
                display: none;
            }

            label{
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;
                position: relative;
                width: 40px;
                height: 40px;
                border-radius: 100px;
                border: 1px solid #ffffff4f;

                color: $white;

                @media screen and (min-width:1064px) {
                   width: 80px;
                   height: 80px;
                }
                &:hover{
                    background-color: #ffffff4f;
                }
            }

            input[type=radio]:checked + label{
                opacity: 1;
                background-color: $white;
                color: $black;
            }
        }

        .techno-content-description{

            @media screen and (min-width:768px) {
                width: 458px;
                margin: 0 auto;
                padding-bottom: 97px;
            }

            @media screen and (min-width:1064px) {
                grid-area: b;
                padding-bottom: 0;
                text-align: left;

            }

            span{
                text-transform: uppercase;
                @include fontStyle(1rem,400,inherit,2.7px);
            }

            h3{
                margin: 9px auto 16px;
            }
        }
    }
}