.navigation-container{
    position: fixed;
    right: -254px;
    top: 0;
    z-index: 100;
    height: 100vh;
    width: 254px;
    transition: all 0.6s ease;
  

    @media screen and (min-width:768px) {
        position: inherit;
        height: 96px;
        width: 456px;
        right: inherit;
    }
    @media screen and (min-width:1064px) {
        position: inherit;
        height: 96px;
        width: 830px;
        right: inherit;
    }
    &::before{
        content: "";
        position: absolute;
        top: 0;
        height: 100vh;
        width: 254px;
        transition: all 0.6s ease;
        background: rgba(255, 255, 255, 0.04);
        backdrop-filter: blur(40.7px) saturate(180%);
        -webkit-backdrop-filter: blur(40.7px) saturate(180%);

        @media screen and (min-width:768px) {
           position: absolute;
           width: 100vw;
           height: 96px;

        }

    }


    .navigation-content{

        margin-left: 32px;
        padding-top: 118px;
        height: 172px;
        display: flex;
        flex-direction: column;
        gap: 32px;

        @media screen and (min-width:768px) {
           flex-direction: row;
           padding-top: 0;
           height: 96px;
           align-items: center;
           justify-content: space-between;
        }

        @media screen and (min-width:1064px) {
            margin-left: 124px;
          width:542px ;
         }

        li a {
            text-transform: uppercase;
            color: $white;
            position: relative;
            
            &::after{
                position: absolute;
                content: "";
                height: 3px;
                width: 100%;
                background: white;
                bottom: -10px;
                left: 0;
                opacity: 0;

                @media screen and (min-width:768px) {
                    bottom: -39px;
                 }
            }

            &:hover::after{
                opacity: 0.5;
            }
            
            span{
                font-weight: 700;
                margin-right: 11px;
                @media screen and (min-width:768px) {
                    display: none;
                 }

                 @media screen and (min-width:1064px) {
                    display: inline;
                 }
            }
        }
        
    }
}

#burger{
    @media screen and (min-width:768px) {
        display: none;
     }
}
.burger-active {
    right: 0;
}


.nav-active{

    &::after{
        position: absolute;
        content: "";
        height: 3px;
        width: 100%;
        background: white;
        bottom: -10px;
        left: 0;
        opacity: 1 !important;
    }
}