.header-container {
  padding-top: 24px;
  @media screen and (min-width: 768px) {
    padding-top: 0;
  }
  @media screen and (min-width: 1064px) {
    padding-top:40px ;
  }

  .header-content {
    display: flex;
    justify-content: space-between;
    position: relative;

    &::after{
        @media screen and (min-width: 1064px) {
            content: "";
            position: absolute;
            z-index: 100;
            left: 104px;
             width: 28%;
            opacity: 0.25;
            height: 1px;
            background-color:white;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    #logo {
      width: 40px;
      position: relative;
      @media screen and (min-width: 1064px) {
        width: 48px;
        }
    }
  }

  #burger {
    position: absolute;
    top: 50%;
    right: 0;
    z-index: 200;
    transform: translateY(-50%);
    height: 21px;
    img {
      width: 24px;
    }
  }
}
