@font-face {
    font-family: 'Bellefair';
    src: url(../assets/fonts/Bellefair-Regular.ttf);
}

@font-face {
    font-family: 'Barlow';
    src: url(../assets/fonts/BarlowCondensed-Regular.ttf);
    font-weight: 400;
}

@font-face {
    font-family: 'Barlow';
    src: url(../assets/fonts/BarlowCondensed-Bold.ttf);
    font-weight: 700;
}

$font1: "Bellefair", serif;
$font2: "Barlow", sans-serif;

$black: #0B0D17;
$white:#FFFFFF;
$color1: #D0D6F9;


@mixin fontStyle($size,$weight,$lineSpace, $letterSpacing){
    font-size: $size;
    font-weight: $weight;
    line-height: $lineSpace;
    letter-spacing: $letterSpacing;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style-type: none;
    outline: none;
    border: none;
    text-decoration: none;
}  

.wrapper{
    width: 100%;
    max-width: 327px;
    margin: 0 auto;
    @media screen and (min-width:768px) {
        max-width: 683px;
    }
    @media screen and (min-width:1064px) {
        max-width:1220px ;
        width: 95%;
    }
}

img{
    object-fit: contain;
}

button{
    background: none;
    outline: none;
    cursor: pointer;
}


body{
    font-family: $font2;
    @include fontStyle(0.938rem,400,25px,0 );
    color: $color1;
    height: 100vh;
    overflow-x:hidden ;
    @media screen and (min-width:768px) {
        @include fontStyle(1rem,400,28px,0 );
     }

     @media screen and (min-width:1064px) {
        @include fontStyle(1.125rem,400,32px,0 );
    }
}

h1,h2, h3, h4{
    font-family: $font1;
    text-transform: uppercase;
    
}

h1{
    @include fontStyle(5rem,400,100px,0 );
    color: $white;
    @media screen and (min-width:768px) {
        @include fontStyle(5rem,400,150px,0 );
     }

     @media screen and (min-width:1064px) {
        @include fontStyle(6.25rem,400,normal,0 );
    }
}

h2{
 @include fontStyle(3.5rem,400,100px,0 );
 color: $white;
}

h3{
    @include fontStyle(1.5rem,400,auto,0 );
    color: $white;

    //tablet
    @media screen and (min-width:768px) {
        @include fontStyle(2.5rem,400,auto,0 );
     }
   }
   

h4{
    opacity: 0.5;
    @include fontStyle(1.25rem,400,normal,0);
    @media screen and (min-width:768px) {
        @include fontStyle(1.5rem,400,normal,0 );
     }
}

h5{
    @include fontStyle(1rem,400,normal,2.7px);
    text-transform: uppercase;
    margin: 24px auto 32px;
    color: $white;

    //tablet
    @media screen and (min-width:768px) {
        margin: 40px auto 60px;
        @include fontStyle(1.25rem,400,normal,2.7px);
     }

      //tablet
    @media screen and (min-width:1064px) {
        margin: 40px auto 60px;
        @include fontStyle(1.75rem,400,normal,2.7px);
     }

    span{
        opacity:0.25;
        font-weight: 700;
    }
}




.subheading-1{
    font-family: $font1;
    @include fontStyle(1.75rem,400,normal,2.7px);
}

.subheading-2{
    text-transform: uppercase;
    font-family: $font2;
    @include fontStyle(0.875rem,400,normal,2.7px);
}

.nav-text{
    @include fontStyle(1rem,400,normal,2.7px);
    @media screen and (min-width:768px) {
        @include fontStyle(0.875rem,400,normal,2.7px);
     }
     @media screen and (min-width:1064) {
        @include fontStyle(1rem,400,normal,2.7px);
     }
}

.nav-planet{
    font-family: $font2;
    @include fontStyle(1rem,400,normal,2.7px);
}