.crew-container {
  background-image: url("../assets/crew/background-crew-mobile.jpg");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 100vh;

   //tablet
   display: flex;
   flex-direction: column;
   @media screen and (min-width:768px) {
    background-image: url("../assets/crew/background-crew-tablet.jpg");
    }

  .crew-content {
    text-align: center;
   
    @media screen and (min-width: 768px) {
     flex: 1;
     display: flex;
     flex-direction: column;
     }

     @media screen and (min-width: 1064px) {
      flex: 1;
       width: 1110px;
      }
    h5{
        @media screen and (min-width: 768px) {
          text-align: left;
           margin:40px 0 60px;
          }
          @media screen and (min-width: 1064px) {
            margin-bottom: 0;
          }
    }
    .crew-content-container {
      
      //tablet
      @media screen and (min-width: 768px) {
        flex: 1;
        display: grid;
        grid-template-areas: "a" "b";
        grid-template-rows: auto 532px;
      }

      @media screen and (min-width:1064px) {
        display: flex;
        flex-direction: row-reverse;
      }
    }

    .crew-radio-description{
       //tablet
      @media screen and (min-width: 768px) {
        display: flex;
        flex-direction: column-reverse;
      }

      @media screen and (min-width: 1064px) {
        text-align: left;
        margin-top: 160px;
        height: 406px;
        position: relative;
        flex-direction: column;
      }
    }

    .img-container {
      width: 327px;
      height: 223px;
      border-bottom: 1px solid #383b4b;
      margin-bottom: 32px;
 
        //tablet
        @media screen and (min-width: 768px) {
           grid-area: b;
            height: 100%; 
            position: relative;
            width: 100%;
            border-bottom: none;
            margin-bottom: 1px;
          }

          @media screen and (min-width: 1064px) {
           position: relative;
           flex: 1;
           display: flex;
           align-self: flex-end;
           margin-bottom: 0;
        }
          

          
      img {
        width: 100%;
        height:100%;
        
        //tablet
        @media screen and (min-width: 768px) {
            object-position:bottom;
            margin-bottom: -12px;
          }
        
        @media screen and (min-width: 1064px) {
            height: 712px;
          align-self: flex-end;
            margin-bottom: 0;
        }
    }
    }

    .radio-container {
      width: 88px;
      display: flex;
      justify-content: space-between;
      margin: 0 auto 32px;

        //tablet
        @media screen and (min-width: 768px) {
            margin-top: 40px;
          }
        
   
        @media screen and (min-width: 1064px) {
         margin: 120px 0 0;
         position: absolute;
         bottom: 0;
        }
      input[type="radio"] {
        display: none;
      }

      label {
        cursor: pointer;
        display: block;
        position: relative;
        width: 10px;
        height: 10px;
        border-radius: 100px;
        background-color: $white;
        opacity: 0.2;

        &:hover {
          opacity: 0.5;
        }
      }

      input[type="radio"]:checked + label {
        opacity: 1;
      }
    }

    .crew-content-description {
        //tablet
      @media screen and (min-width: 768px) {
        width: 458px;
        margin: 0 auto;
      }

      @media screen and (min-width: 1064px) {
        position: relative;
        
        p{
          width: 444px;
        }
      }
      h3 {
        margin: 8px auto 16px;

        @media screen and (min-width: 768px) {
            margin: 10px auto 20px;
          }

          @media screen and (min-width: 768px) {
            margin: 15px auto 42px;
          }
      }
    }
  }
}
