.destination-container{
    background-image: url("../assets/destination/background-destination-mobile.jpg");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;

    //tablet
    @media screen and (min-width:768px) {
        background-image: url("../assets/destination/background-destination-tablet.jpg");
    }

    @media screen and (min-width:1064px) {
        background-image: url("../assets/destination/background-destination-desktop.jpg");
    }

    .destination-content{
        text-align: center;
        padding-bottom: 56px;
        @media screen and (min-width:1064px) {
            width: 1110px;
         }
        h5{
             //tablet
            @media screen and (min-width:768px) {
            text-align: left;
         }
        }

        .main-content{
            @media screen and (min-width:1064px) {
               display: flex;
               width: 1047px;
               justify-content: space-between;
               text-align: left;
            }

             > div{
                @media screen and (min-width:1064px) {
                    width: 445px;
                    height: 478px;
                }
               
            }
        }
        img{
            width: 170px;
            @media screen and (min-width:768px) {
                width: 300px;
             }
             @media screen and (min-width:1064px) {
                width: 445px;
                align-self: flex-end;
             }
        }

        .button-container{
         margin: 53px auto 32px;
          color: $white;  
          display: flex;
          justify-content: space-between;
          width: 237.5px;
          @media screen and (min-width:768px) {
            width: 285.5px;
         }

         @media screen and (min-width:1064px) {
            margin: 0 0 37px;
         }

          button {
            color:$color1;

            position: relative;
            &::after{
                position: absolute;
                content: "";
                height: 3px;
                width: 100%;
                background: white;
                bottom: -10px;
                left: 0;
                opacity: 0;
            }

            &:hover::after{
                opacity: 0.5;
            }
          }
        }

        button.active{
            position: relative;
            color: $white;
            &::after{
                position: absolute;
                content: "";
                height: 3px;
                width: 100%;
                background: white;
                bottom: -10px;
                left: 0;
                opacity: 1 !important;
            }
        }


        .main-description{
            padding-bottom: 32px;
            border-bottom: 2px solid #383B4B;
            margin-bottom:32px ;
            @media screen and (min-width:768px) {
                width: 573px;
                margin: 0 auto 32px;
             }

             @media screen and (min-width:1064px) {
                width: 100%;
                margin: 0 auto 32px;
                padding-bottom: 54px;
             }
        }


        .footer-description{
            @media screen and (min-width:768px) {
                width: 573px;
                margin: 0 auto 32px;
                display: flex;
                justify-content: space-around;
             }

            @media screen and (min-width:1064px) {
                margin: 0;
                justify-content: flex-start;
                gap:79px;
            }
        }
        .distance, .average{
            text-transform: uppercase;
            span{
                margin-top:12px ;
                display: block;
                @media screen and (min-width:1064px) {
                    margin: 0;
               
                }
            }

            & span:nth-child(2){
                color: $white ;
                @media screen and (min-width:1064px) {
                    margin-top: 12px;
                }
            }
        }

        

        .distance{
            margin-bottom: 32px;
        }

    }
}